<template>
  <div class="Search">
    <HomeLink></HomeLink>
    <div class="ipkeyword">
      <div>
        <input type="text" placeholder="搜索歌曲、歌手、专辑" v-model="keyword" />
      </div>
      <span @click="keyword=''" v-if="keyword != ''"></span>
    </div>
    <!-- 热门搜索词 -->
    <div class="hotSear" v-if="keyword == ''">
      <h5>热门搜索词</h5>
      <div>
        <span
          v-for="(item, index) in hotSear"
          :key="index"
          @click="keyword=item.searchWord"
        >{{item.searchWord}}</span>
      </div>
    </div>
    <!-- 推荐搜索 -->
    <div class="recommend" v-if="keyword != ''&&recoseen">
      <!-- 加载动画 -->
      <div class="loing" v-if="recommend == null">
        <img src="../assets/loading.svg" />
      </div>
      <div class="olone" @click="musicGo(keyword)">搜索:"{{keyword}}"</div>
      <div class="bottom">
        <div
          v-for="(item, index) in recommend"
          :key="index"
          @click="musicGo(item.keyword)"
        >{{item.keyword}}</div>
      </div>
    </div>
    <!-- 搜索列表 -->
    <div class="SonList">
      <!-- 加载动画 -->
      <div class="loing" v-if="longseen">
        <img src="../assets/loading.svg" />
      </div>
      <thesearchList
        v-for="(item, index) in searchList"
        :key="index"
        :muData="item"
        :currentSong="currentSong"
        @tanslate-song="$emit('tanslate-song', $event)"
      ></thesearchList>
    </div>
  </div>
</template>

<script>
import HomeLink from "@/components/HomeLink.vue";
import thesearchList from "@/components/searchList.vue";

export default {
  name: "Search",
  props: {
    currentSong: Object
  },
  components: {
    HomeLink,
    thesearchList
  },
  data: function() {
    return {
      keyword: "",
      hotSear: null,
      recommend: null,
      searchList: null,
      longseen: false,
      recoseen: true
    };
  },
  watch: {
    keyword: function() {
      this.recommend = null;
      this.searchList = null;
      //显示推荐
      this.recoseen = true;
      if (this.keyword === "") {
        return;
      }
      this.axios
        .get("/search/suggest?keywords=" + this.keyword + "&type=mobile")
        .then(response => {
          this.recommend = response.data.result.allMatch;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  methods: {
    //获取音乐列表
    musicGo: function(keyword = this.keyword) {
      //显示加载动画
      this.longseen = true;
      //隐藏推荐列表
      this.recoseen = false;
      this.searchList = null;
      this.axios
        .get("/search?keywords=" + keyword)
        .then(response => {
          this.searchList = response.data.result.songs;
          console.log(response);
          
          this.longseen = false;
          window.localStorage.setItem(
            "searchList",
            JSON.stringify({
              expire: Date.now() + 1 * 60 * 60 * 1000,
              result: response.data.result.songs
            })
          );
        })
        .catch(function(error) {
          this.longseen = false;
          console.log(error);
        });
    }
  },
  created() {
    //获取热门搜索词
    this.axios
      .get("/search/hot/detail")
      .then(response => {
        this.hotSear = response.data.data;
        window.localStorage.setItem(
          "hotSear",
          JSON.stringify({
            expire: Date.now() + 1 * 60 * 60 * 1000,
            result: response.data.data
          })
        );
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>


<style lang="less">
.Search {
  height: 100%;
  background-color: #fcfcfd;
  overflow: hidden;
  .ipkeyword {
    position: relative;
    height: 60px;
    padding: 15px 10px;
    border-bottom: #eee solid 1px;
    &::before {
      content: "";
      width: 18px;
      height: 18px;
      display: block;
      position: absolute;
      background-image: url(../assets/magnifier.svg);
      top: 22px;
      left: 17px;
    }
    span {
      content: "";
      width: 15px;
      height: 15px;
      display: block;
      position: absolute;
      background-image: url(../assets/delete.svg);
      top: 23px;
      right: 18px;
    }
    > div {
      background: #ebecec;
      border-radius: 50px;
      display: flex;
      justify-content: center;
    }
    input {
      width: 290px;
      height: 30px;
      border: none;
      background: #ebecec;
      font-size: 14px;
      outline: none;
    }
  }
}

.hotSear {
  padding: 15px 10px 0;
  h5 {
    font-size: 12px;
  }
  span {
    font-size: 14px;
    display: inline-block;
    margin: 8px;
    padding: 5px 10px;
    border: #ccc 1px solid;
    border-radius: 50px;
  }
}

.recommend {
  .yangs {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #eee;
    font-size: 15px;
  }
  .bottom {
    div {
      display: flex;
      align-items: center;
      .yangs();
      &::before {
        content: "";
        margin: 10px;
        width: 15px;
        height: 15px;
        display: block;
        background-image: url(../assets/magnifier.svg);
      }
    }
  }
  .olone {
    .yangs();
    color: skyblue;
    padding-left: 10px;
  }
}

.loing {
  img {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
}

.SonList {
  width: 100%;
}
</style>