
<template>
  <div class="playbar">
    <div class="playbarbottom" @click="showFullscreen=true">
      <img class="thumb" :class="{active: playing}" :src="imgHandle" alt />
      <div class="info">
        <h5>{{currentSong.name}}</h5>
        <span class="word" v-if="parsedLyric">{{parsedLyric[currentLyricIndex].text}}</span>
      </div>
      <div class="progress" @click.stop="playing = !playing">
        <canvas width="36" height="36" id="canvas"></canvas>
        <i v-if="playing" class="fa fa-pause"></i>
        <i v-else class="fa fa-play"></i>
      </div>
      <div class="playlist" @click.stop="showPlayList=!showPlayList">
        <i class="fa fa-bars" aria-hidden="true"></i>
      </div>
    </div>

    <!-- 播放列表 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animated slideInUp"
      leave-active-class="animated slideOutDown"
    >
      <ul v-if="showPlayList" class="defaultPlayList">
        <SongItem
          v-for="(item, index) in defaultPlayList"
          :key="index"
          :songItem="item"
          :options="{order: index, info: false}"
          @tanslate-song="$emit('tanslate-song', $event)"
          :currentSong="currentSong"
        ></SongItem>
      </ul>
    </transition>

    <!-- 歌词 -->
    <transition
      name="custom-classes-transition"
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
    >
      <div v-show="showFullscreen" class="fullscreenplay">
        <div class="mask" :style="{backgroundImage: `url(${currentSong.picUrl})`}"></div>
        <header class="hea-top">
          <i class="fa fa-arrow-left" @click="showFullscreen=false"></i>
          <span>{{currentSong.name}}</span>
        </header>
        <!-- 音量 -->
        <div class="volume">
          <input type="range" max="100" min="0" v-model="volume" />
          <div class="tis">音量:</div>
          <div class="voSeen">
            <div :style="{width:volume+'%'}"></div>
            <span></span>
          </div>
        </div>
        <div
          v-show="showCircle"
          class="circle"
          :class="{active: playing}"
          @click="showCircle = false"
        >
          <div class="needle"></div>
          <div class="glue">
            <img :src="currentSong.picUrl" alt />
          </div>
        </div>
        <div v-show="!showCircle" class="lyric-box" @click="showCircle = true">
          <ul class="scroll" :style="{transform: `translateY(${-currentLyricIndex * 28}px)`}">
            <li
              v-for="(item, index) in parsedLyric"
              :class="{active: index===currentLyricIndex}"
              :key="index"
            >{{item.text}}</li>
          </ul>
        </div>

        <footer>
          <!-- 进度条 -->
          <div class="foo-top">
            <span>{{itemsnow}}</span>
            <div class="ip">
              <div class="jind">
                <div :style="{width:sofp+'%'}"></div>
                <span></span>
              </div>
              <input type="range" max="100" min="0" v-model="sofp" @input="Speedofprogress" />
            </div>
            <span>{{timeToMinute(currentSong.song.duration / 1000)}}</span>
          </div>

          <div class="botton">
            <div class="l" @click="goToMusic(false)">
              <i class="fa fa-arrow-left"></i>
            </div>

            <div class="open" @click="playing=!playing">
              <i v-if="playing" class="fa fa-pause"></i>
              <i v-else class="fa fa-play"></i>
            </div>

            <div class="r" @click="goToMusic">
              <i class="fa fa-arrow-right"></i>
            </div>
          </div>
        </footer>
      </div>
    </transition>

    <audio :src="currentSongUrl" autoplay controls height="30"></audio>
  </div>
</template>

<script>
import SongItem from "@/components/SongItem";

export default {
  props: {
    currentSong: Object
  },
  components: {
    SongItem
  },
  data: function() {
    return {
      sofp: 0,
      volume: 100,
      itemsnow: "00:00",
      // -----
      playing: false,
      showFullscreen: false,
      showCircle: false,
      currentLyric: null,
      currentLyricIndex: 0,
      showPlayList: false,
      defaultPlayList: [this.currentSong]
    };
  },
  methods: {
    goToMusic(direction = true) {
      if (direction)
        this.$emit(
          "tanslate-song",
          this.defaultPlayList[
            this.musicIndex + 1 >= this.defaultPlayList.length - 1
              ? 0
              : this.musicIndex + 1
          ]
        );
      else
        this.$emit(
          "tanslate-song",
          this.defaultPlayList[
            this.musicIndex - 1 <= 0
              ? this.defaultPlayList.length - 1
              : this.musicIndex - 1
          ]
        );
    },
    joinArtists: function(artists) {
      return artists.map(artist => artist.name).join(" / ");
    },
    drawProgress: function() {
      var canvas = this.$el.querySelector("#canvas");
      var audio = this.$el.querySelector("audio");
      var duration = this.currentSong.song.duration;
      console.log(duration);
      audio.ontimeupdate = () => {
        /** @type {HTMLCanvasElement} */
        // console.log(canvas);
        // 获取canvas元素
        var context = canvas.getContext("2d");
        // console.log(duration, this.currentTime * 1000);
        // console.log((this.currentTime * 100) / duration);
        context.clearRect(0, 0, 36, 36);
        context.beginPath();
        context.arc(18, 18, 17, 0, 2 * Math.PI);
        context.closePath();
        context.strokeStyle = "gray";
        // 描边样式
        context.lineWidth = "1";
        // 描边宽度
        context.stroke(); // 路径描边
        context.beginPath();
        context.arc(
          18,
          18,
          17,
          -0.5 * Math.PI,
          -0.5 * Math.PI + 2 * Math.PI * ((audio.currentTime * 1000) / duration)
        );
        // context.closePath();
        context.strokeStyle = "rgba(255,0,0,0.7)";
        // 描边样式
        context.lineWidth = "1";
        // 描边宽度
        context.stroke(); // 路径描边
        // 滚动歌词
        // this.currentTime
        // currentLyricIndex
        let index;
        for (let i = 0; i < this.parsedLyric.length; i++) {
          if (audio.currentTime + 0.15 < this.parsedLyric[i].time) {
            index = i - 1;
            break;
          }
        }
        if (index === undefined) {
          index = this.parsedLyric.length - 1;
        }
        this.currentLyricIndex = index;

        this.sofp = Math.floor(((audio.currentTime * 1000) / duration) * 100);

        // console.log(((audio.currentTime * 1000) / duration) * 100);
      };
    },
    getLyric: function() {
      var cachedLyric = window.localStorage.getItem(
        "lyric" + this.currentSong.id
      );
      if (cachedLyric) {
        this.currentLyric = cachedLyric;
      } else {
        window.axios
          .get("lyric", {
            params: {
              id: this.currentSong.id
            }
          })
          .then(response => {
            this.currentLyric = response.data.lrc.lyric;
            window.localStorage.setItem(
              "lyric" + this.currentSong.id,
              response.data.lrc.lyric
            );
          });
      }
    },
    //时间转化
    timeToMinute(times) {
      if (!times) {
        return null;
      }
      var t;
      if (times > -1) {
        var hour = Math.floor(times / 3600);
        var min = Math.floor(times / 60) % 60;
        var sec = times % 60;
        if (hour < 10) {
          t = "0" + hour + ":";
        } else {
          t = hour + ":";
        }

        if (min < 10) {
          t += "0";
        }
        t += min + ":";
        if (sec < 10) {
          t += "0";
        }
        t += sec.toFixed(2);
      }
      t = t.substring(0, t.length - 3);
      t = t.slice(3);
      return t;
    },
    //t拖动进度条
    Speedofprogress() {
      var duration = this.currentSong.song.duration;
      var audio = this.$el.querySelector("audio");
      var now = (duration * (this.sofp / 100)) / 1000;
      audio.currentTime = now;
      this.itemsnow = this.timeToMinute(audio.currentTime);
    }
  },
  computed: {
    musicIndex: function() {
      let list = this.defaultPlayList;
      let currentSong = this.currentSong;
      for (let i = 0; i < list.length; i++) {
        if (list[i].id == currentSong.id) {
          return i;
        }
      }
      return 0;
    },
    currentSongUrl: function() {
      if (this.currentSong)
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
      else return "";
    },
    parsedLyric: function() {
      if (this.currentLyric) {
        return this.currentLyric.split("\n").map(item => {
          // console.log(item);
          var regex = /\d{2}:\d{2}\.\d+/i;
          if (item.search(regex) !== -1) {
            var time = item.match(regex)[0];
            var m = time.substr(0, 2);
            var s = time.substr(3, 2);
            var n = time.substr(5);
            return {
              time: Number(m) * 60 + Number(s) + Number(n),
              text: item.substr(11) || "---------"
            };
          } else {
            return {};
          }
        });
      } else {
        return null;
      }
    },
    imgHandle: function() {
      if (this.currentSong.picUrl) return this.currentSong.picUrl;
      else return "../assets/default.jpg";
    }
  },
  mounted() {
    var audio = this.$el.querySelector("audio");
    this.drawProgress();
    audio.onplay = () => {
      console.log("开始播放");
      this.playing = true;
    };
    audio.onpause = () => {
      console.log("暂停播放");
      this.playing = false;
    };
    audio.onended = () => {
      this.goToMusic();
    };
  },
  watch: {
    volume: function() {
      var audio = this.$el.querySelector("audio");
      audio.volume = this.volume / 100;
    },
    sofp: function() {
      var audio = this.$el.querySelector("audio");
      this.itemsnow = this.timeToMinute(audio.currentTime);
    },
    playing: function(value) {
      var audio = this.$el.querySelector("audio");
      if (value) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    currentSong: function(value) {
      this.getLyric();
      // 加入列表 需要过滤重复
      // this.defaultPlayList.push(value);
      let isExist = this.defaultPlayList.some(item => {
        return item.id == value.id;
      });
      console.log(isExist);
      if (!isExist) this.defaultPlayList.push(value);
    }
  },
  created() {
    this.getLyric();
  }
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.volume {
  display: flex;
  justify-content: center;
  position: relative;
  margin-bottom: 10px;
  .tis {
    color: #fff;
    font-size: 12px;
    line-height: 5px;
    margin: 0 10px;
  }
  input {
    width: 70%;
    position: absolute;
    transform: translate(25px, -10px);
    opacity: 0;
    z-index: 1000;
  }
  .voSeen {
    width: 70%;
    height: 2px;
    background-color: #fff;
    display: flex;
    z-index: -1;
    > div {
      height: 100%;
      background-color: orange;
    }
    > span {
      display: inline-block;
      width: 10px;
      height: 10px;
      background-color: #fff;
      border-radius: 50%;
      transform: translate(-0px, -3px);
    }
  }
}
.word {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  max-width: 200px;
  // background-color: #000;
  z-index: -1;
}
.botton {
  width: 100%;
  height: 50px;
  // background-color: #fff;
  color: skyblue;
  display: flex;
  justify-content: center;
  align-items: center;
  div {
    margin: 0px 30px;
  }
}
.hea-top {
  i {
    margin: 10px;
  }
  color: #fff;
}
.playbar {
  z-index: 1000;
}
.playbarbottom {
  padding: 5px 0;
  background: white;
  border-top: 1px solid rgba(128, 128, 128, 0.2);
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  .margin () {
    margin: 0 10px;
  }
  .thumb {
    .margin;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    animation: rotate 6s linear infinite;
    animation-play-state: paused;
    &.active {
      animation-play-state: running;
    }
  }
  .info {
    flex: 1;
    font-size: 12px;
    line-height: 18px;
    span {
      color: gray;
    }
  }
  .progress {
    .margin;
    width: 28px;
    height: 28px;
    // background: lightgray;
    position: relative;
    #canvas {
      width: 100%;
    }
    i {
      position: absolute;
      top: 50%;
      left: 50%;
      color: lightgray;
      font-size: 12px;
      display: block;
      margin-top: -6px;
      margin-left: -6px;
      line-height: 1em;
      width: 12px;
      height: 12px;
      text-align: center;
    }
  }
  .playlist {
    .margin;
    width: 28px;
    height: 28px;
    // background: red;
    position: relative;
    border: 1px solid lightgray;
    border-radius: 50%;
    i {
      position: absolute;
      top: 50%;
      left: 50%;
      color: lightgray;
      font-size: 12px;
      display: block;
      margin-top: -6px;
      margin-left: -6px;
      line-height: 1em;
      width: 12px;
      height: 12px;
      text-align: center;
    }
  }
}
.defaultPlayList {
  position: fixed;
  bottom: 50px;
  left: 2.5%;
  width: 95%;
  background: white;
  border-radius: 15px;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.3);
  z-index: 9;
}
.fullscreenplay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  .mask {
    filter: blur(30px) brightness(0.5);
    transform: scale(2);
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    z-index: -1;
  }
  header {
    height: 50px;
    // background: lightblue;
  }
  .circle {
    flex: 1;
    text-align: center;
    position: relative;
    .glue {
      padding: 54px;
      // background: red;
      background-image: url("../assets/disc_light-ip6.png"),
        url("../assets/disc-ip6.png");
      background-size: 100%;
      border-radius: 50%;
      display: inline-block;
      margin-top: 84px;
      animation: rotate 6s linear infinite;
      animation-play-state: paused;
      img {
        width: 180px;
        height: 180px;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
    .needle {
      width: 96px;
      height: 137px;
      background-image: url("../assets/needle.png");
      // background-color: lightblue;
      background-size: 100%;
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 1;
      margin-left: -16px;
      transform: rotate(-12deg);
      transform-origin: 16px 0;
      transition: all 0.3s;
    }
    &.active {
      .needle {
        transform: rotate(0deg);
      }
      .glue {
        animation-play-state: running;
      }
    }
  }
  .lyric-box {
    flex: 1;
    overflow: hidden;
    position: relative;
    .scroll {
      text-align: center;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 50%;
      margin-top: -1em;
      transition: transform 0.3s;
      li {
        line-height: 2em;
        font-size: 14px;
        height: 2em;
        color: gray;
        &.active {
          color: white;
        }
      }
    }
  }
  footer {
    height: 100px;
    // background: lightblue;
  }
}
.fadeIn {
  animation-duration: 0.3s;
}

.foo-top {
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  > span {
    width: 100px;
    height: 30px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    font-size: 12px;
  }
}
.ip {
  width: 100%;
  height: 30px;
  display: grid;
  justify-content: center;
  position: relative;
  .jind {
    transform: translateY(20px);
    z-index: -1;
    width: 260px;
    height: 2px;
    background-color: #fff;
    display: flex;
    > div {
      height: 100%;
      background-color: skyblue;
    }
    > span {
      width: 10px;
      height: 10px;
      background-color: skyblue;
      border-radius: 50%;
      display: block;
      transform: translateY(-3px);
    }
  }
  input {
    width: 260px;
    opacity: 0;
  }
}
</style>