<template>
  <div class="searchList" @click="$emit('tanslate-song', fanData)">
    <div v-if="order!== undefined" class="Sumb">{{order}}</div>
    <div class="inf">
      <h3>
        {{muData.name}}
        <!-- <span>{{muData}}</span> -->
      </h3>
      <h5>
        <span class="sq">1</span>
        {{joinArtists(muData.artists)}}
      </h5>
    </div>
    <div class="ico-svg" v-if="currentSong && muData.id === currentSong.id">
      <img style="background:none" src="../assets/playing.svg" alt />
    </div>

    <div v-else class="ico"></div>
  </div>
</template>
    
<script>
export default {
  name: "searchList",
  props: {
    order: Number,
    songItem: Object,
    muData: Object,
    currentSong: Object
  },
  data: function() {
    return {
      fanData: null
    };
  },
  methods: {
    joinArtists: function(muData) {
      return muData.map(artist => artist.name).join(" / ");
    }
  },
  created() {
    //获取歌曲详情
    this.axios
      .get("/song/detail?ids=" + this.muData.id)
      .then(response => {
        let getData = response.data;

        this.fanData = {
          name: getData.songs[0].name,
          picUrl: getData.songs[0].al.picUrl,
          id: this.muData.id,
          song: {
            alias: getData.songs[0].ar,
            album: getData.songs[0].al,
            duration: this.muData.duration
          }
        };
      })
      .catch(function(error) {
        console.log(error);
      });
  }
};
</script>

<style lang='less' >
.searchList {
  width: 100%;
  overflow: hidden;
  min-width: 300px;
  height: 55px;
  box-sizing: border-box;
  border-bottom: 1px #eee solid;
  /* background-color: #908765; */
  display: flex;
  .inf {
    margin-left: 10px;
    padding-left: 10px;
    padding: 6px 0px;
  }
  .Sumb {
    margin-right: -10px;
    width: 40px;
    text-align: center;
    line-height: 55px;
    color: #888;
  }
  .ico {
    width: 45px;
    height: 54px;
    font-size: 22px;
    position: absolute;
    background-color: #ffffff;
    right: 0px;
    background-image: url(../assets/index_icon_2x.png);
    background-size: 166px auto;
    background-position: -12px 15px;
  }
  .ico-svg {
    width: 45px;
    height: 54px;
    position: absolute;
    right: 0px;
    display: flex;
    justify-content: center;
    img {
      width: 25px;
    }
  }
  .sq {
    display: inline-block;
    color: rgba(0, 0, 0, 0);
    height: 100%;
    width: 15px;
    background-image: url(../assets/index_icon_2x.png);
    background-size: 166px auto;
    background-position: 2px 8px;
  }
  .name {
    display: flex;
    white-space: nowrap;
  }
  h3 {
    width: 0;
    white-space: nowrap;
    text-overflow: ellipsis;
    span {
      color: rgb(189, 189, 189);
    }
  }
  h5 {
    width: 0;
    font-size: 12px;
    color: #888;
    line-height: 24px;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
</style>