<template>
  <div class="sibtSList">
    <searchList
      v-for="(item, index) in tonData"
      @tanslate-song="$emit('tanslate-song', $event)"
      :order="index+1"
      :muData="item"
      :key="index"
    ></searchList>
  </div>
</template>

<script>
import searchList from "@/components/searchList";

//tracks
export default {
  props: {
    tracks: Array
  },
  components: {
    searchList
  },
  data: function() {
    return {
      currentSong: null,
      tonData: []
    };
  },
  methods: {
    GetData() {
      this.tonData = [];
      for (let i = 0; i < this.tracks.length; i++) {
        this.tonData.push({
          name: this.tracks[i].name,
          picUrl: this.tracks[i].al.picUrl,
          id: this.tracks[i].id,
          artists: this.tracks[i].ar
        });
      }
    }
  },
  watch: {
    tracks: function() {
      this.GetData();
    }
  }
};
</script>
