<template>
  <div id="app" @touchstart="touchstart" @touchend="touchend">
    <!-- 一级路由出口 -->
    <keep-alive>
      <router-view @tanslate-song="currentSong = $event" :currentSong="currentSong"></router-view>
    </keep-alive>

    <!-- 播放器 边栏 -->
    <PlayBar
      @tanslate-song="currentSong = $event"
      v-if="currentSong"
      :currentSong="currentSong"
    ></PlayBar>
  </div>
</template>
<script>
import PlayBar from "@/components/PlayBar";

export default {
  components: {
    PlayBar
  },
  data: function() {
    return {
      searson: null,
      currentSong: null,
      touchstartX: 0
    };
  },

  methods: {
    touchstart: function(event) {
      this.touchstartX = event.changedTouches[0].clientX;
      this.touchstartY = event.changedTouches[0].clientY;
    },
    touchend: function(event) {
      if (event.changedTouches[0].clientX - this.touchstartX > 50) {
        this.touchstartX = 0;
        this.touchstartY = 0;
        this.routerBack();
      }

      if (event.changedTouches[0].clientX - this.touchstartX < -50) {
        this.routerForward();
      }
    },
    routerBack: function() {
      this.$router.back();
    },
    routerForward: function() {
      this.$router.forward();
    }
  }
};
</script>
<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding-bottom: 50px;
}
</style>
