<template>
  <div class="Hot">
    <HomeLink></HomeLink>
    <div class="inf">
      <div class="img">
        <div class="hoticon"></div>
        <p>更新日期</p>
      </div>
    </div>
    <div class="list">
      <searchList
        v-for="(item, index) in HotData"
        :order="index+1"
        :muData="item"
        :key="index"
        :currentSong="currentSong"
        @tanslate-song="$emit('tanslate-song', $event)"
      ></searchList>
    </div>
  </div>
</template>
<script>
import HomeLink from "@/components/HomeLink.vue";
import searchList from "@/components/searchList.vue";

export default {
  name: "Hot",
  props: {
    currentSong: Object
  },
  data: function() {
    return {
      HotData: [],
      inLonk: null
    };
  },
  methods: {
    // 获取热歌榜
    getHotData: function() {
      this.axios
        .get("/top/list?idx=1")
        .then(response => {
          this.HotData = response.data.privileges;
          let arrId = "";
          for (let i = 0; i < 20; i++) {
            if (i == 19) {
              arrId += this.HotData[i].id;
              continue;
            }
            arrId += this.HotData[i].id + ",";
          }
          this.axios.get("/song/detail?ids=" + arrId).then(re => {
            this.HotData = re.data.songs;
            this.inLonk = re;
            for (let i = 0; i < this.HotData.length; i++) {
              this.HotData[i] = {
                name: this.HotData[i].name,
                picUrl: this.HotData[i].al.picUrl,
                id: this.HotData[i].id,
                artists: this.HotData[i].ar
              };
            }
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  created() {
    this.getHotData();
  },
  components: {
    HomeLink,
    searchList
  }
};
</script>

<style lang="less">
.Hot {
  overflow: hidden;
  .img {
    width: 100%;
    height: 150px;
    background: url(../assets/Hotbg.jpg);
    background-size: contain;
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    p {
      margin-top: 8px;
      color: hsla(0, 0%, 100%, 0.8);
      font-size: 12px;
    }
    .hoticon {
      width: 142px;
      height: 67px;
      background: url(../assets/index_icon_2x.png) no-repeat;
      background-position: -20px -24px;
      background-size: 150px;
    }
  }
}
</style>